import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)

import $ from 'jquery'
window.$ = $

import MuseUi from 'muse-ui'
Vue.use(MuseUi)

import index from './views/index.vue'

var router = new VueRouter({
	routes: [{
		path: '/index',
		component: index
	}]
})

new Vue({
	el : '#demo',
	router,
})


